export default class GraphService {
  constructor() {
    this.graphUrl = 'https://graph.microsoft.com/v1.0/me/joinedTeams';
    this.campusWebUrl = 'https://campuswebapi-dev.azurewebsites.net/api/IPFeedbackADO/GetWorkIDs';
    this.campusWikiUrl = "https://almsearch.dev.azure.com/servicesdocs/DevOps/_apis/search/wikisearchresults?api-version=5.0-preview.1";

  }

  getTeamsData = token => {
    const headers = new Headers({ Authorization: `Bearer ${token}` });
    const options = {
      headers
    };
    return fetch(`${this.graphUrl}`, options)
      .then(response => response.json())
      .catch(response => {
        throw new Error(response.text());
      });
  };

  getWikiAPIData = (token, f) => {
    // const headers = new Headers({ Authorization: `Bearer ${token}` , "Accept": "application/json;odata=verbose",
    // "Content-type": "application/json;odata=verbose"});
    const options = {

      //body: JSON.stringify({"ProjectSiteUrl":"","IsGapFeedback":false,"IsGapMitigate":false}),
      body: JSON.stringify({ "searchText": f, "$skip": 0, "$top": 100, "$orderBy": null, "includeFacets": true }),
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Headers': '*',
        'timeout': '150000'
      },
      method: 'POST'
    };
    options.headers.Authorization = `Bearer ${token}`;
    return fetch(`${this.campusWikiUrl}`, options)
      .then(response => response.json())
      .catch(response => {
        throw new Error(response.text());
      });
  };

  getCampusWebAPIData = token => {
    const headers = new Headers({
      Authorization: `Bearer ${token}`, "Accept": "application/json;odata=verbose",
      "Content-type": "application/json;odata=verbose"
    });
    const options = {
      headers,
      body: JSON.stringify({ "ProjectSiteUrl": "", "IsGapFeedback": false, "IsGapMitigate": false }),
      method: 'POST'
    };
    return fetch(`${this.campusWebUrl}`, options)
      .then(response => response.json())
      .catch(response => {
        throw new Error(response.text());
      });
  };

}








