import * as Msal from 'msal';

export default class AuthService {
  constructor() {
    let redirectUri = window.location.origin;
    this.applicationConfig = {
      clientID: 'e0f60ff0-327b-43e2-9055-54782bbed141',
      graphScopesForCampusWebAPI: ['https://campuswebapi.azurewebsites.net/user_impersonation'],      
      graphScopesForWikiAPI: ['499b84ac-1321-427f-aa17-267ca6975798/user_impersonation'],
      graphScopes:['user.read']
    };
    this.app = new Msal.UserAgentApplication(
      {
        auth: {
          clientId: this.applicationConfig.clientID,
          redirectUri: redirectUri,
          postLogoutRedirectUri: window.location.origin
        },
        "system": {
          "loadFrameTimeout": 10000
        },
        cache: {
          cacheLocation: "localStorage"
          , storeAuthStateInCookie: true
        }
      }
    );
    this.app.handleRedirectCallback(this.authCallback);
  }

  authCallback(error, response) {
    console.log('authCallback => error : ', error);
    console.log('authCallback => response : ', response);
  }

  getAuthenticationParameters(type) {    
    const user = this.app.getAccount();
    let authenticationParameters = null;
    let scope=null;
    if(type=='team')
    scope = this.applicationConfig.graphScopes;
    else if (type == 'wiki')    
    scope = this.applicationConfig.graphScopesForWikiAPI;
    else
    scope = this.applicationConfig.graphScopesForCampusWebAPI;

    if (user && user.userName) {
      authenticationParameters = {
        scopes: scope
        , loginHint: user.userName
      };
    }
    else {
      authenticationParameters = {
        scopes: scope
      };
    }
    return authenticationParameters;
  }

  getTokenWithLoginPopup = (type) => {
    let authenticationParameters = this.getAuthenticationParameters(type);
    return this.app.acquireTokenSilent(authenticationParameters).then(
      accessToken => {
        console.log("acquireTokenSilent() succeeded.");
        return accessToken.accessToken;
      }
    ).catch(error => {
      console.log("acquireTokenSilent() error : ", error);
      let authenticationParameters = this.getAuthenticationParameters(type);
      return this.app.loginPopup(authenticationParameters)
        .then(
          idToken => {
            console.log("acquireTokenSilent() => loginPopup() succeeded : ", error);
            const user = this.app.getAccount();
            if (user) {
              return this.getTokenWithLoginPopup(type);
            } else {
              return null;
            }
          }
        ).catch(e => {
          console.log("acquireTokenSilent() => loginPopup() error : ", e);
          console.error(e);
        });
    }
    );
  }

   getTokenWithLoginRedirect = (type) => {
    let authenticationParameters = this.getAuthenticationParameters(type);
    return this.app.acquireTokenSilent(authenticationParameters).then(
      accessToken => {
        console.log("acquireTokenSilent() succeeded.");
        return accessToken.accessToken;
      }
    ).catch(error => {
      console.log("acquireTokenSilent() error : ", error);
      let authenticationParameters = this.getAuthenticationParameters(type);
      return this.app.loginRedirect(authenticationParameters);
    }
    );
  }


  getTokenWithPopup = (type) => {
    let authenticationParameters = this.getAuthenticationParameters(type);
    return this.app.acquireTokenSilent(authenticationParameters).then(
      accessToken => {
        console.log("acquireTokenSilent() succeeded.");
        return accessToken.accessToken;
      }
    ).catch(error => {
      console.log("acquireTokenSilent() error : ", error);
      let authenticationParameters = this.getAuthenticationParameters(type);
      return this.app.acquireTokenPopup(authenticationParameters)
        .then(
          idToken => {
            console.log("acquireTokenSilent() => loginPopup() succeeded : ", error);
            const user = this.app.getAccount();
            if (user) {
              return this.getTokenWithPopup();
            } else {
              return null;
            }
          }
        ).catch(e => {
          console.log("acquireTokenSilent() => loginPopup() error : ", e);
          console.error(e);
        });
    }
    );
  }

  getTokenWithRedirect = (type) => {
    let authenticationParameters = this.getAuthenticationParameters(type);
    return this.app.acquireTokenSilent(authenticationParameters).then(
      accessToken => {
        console.log("acquireTokenSilent() succeeded.");
        return accessToken.accessToken;
      }
    ).catch(error => {
      console.log("acquireTokenSilent() error : ", error);
      let authenticationParameters = this.getAuthenticationParameters(type);
      return this.app.acquireTokenRedirect(authenticationParameters);
    }
    );
  }
}



