import React from 'react';
import AuthService from './services/auth.service';
import GraphService from './services/graph.service';

class WikiCode extends React.Component {
  constructor(props) {
    super(props);

    this.authService = new AuthService();
    this.graphService = new GraphService();
    this.state = {
      showFeedBackPanel: false,
      showIPKitPanel: false,
      currFeedbackTitle: 'Test',
      myInputData: '',
      resultData: [],
      resultIPKitData: [],
      srNo: -1,
      isLogin: true,
      selectedFile: null,
      isdocuploaded: false,
      showRating: 0,
      selectedIndex: -1,
      SearchType: 'Wiki Search',
      SearchTypeCode: 'ws',
      currPage: 1,
      tat: ''

    }
  }



  getUserInfo(token, type) {
    this.graphService.getWikiAPIData(token,this.state.myInputData).then(
      data => {
        this.onDataChange1(data);
      },
      error => {
        console.error(error);
        this.setState({
          apiCallFailed: true
        });
      }
    );
  }

  callLoginPopupAPI = (e, type) => {
    this.setState({ apiCallFailed: false });
    this.authService.getTokenWithLoginPopup(type).then(
      token => {
        this.getUserInfo(token, type);
      },
      error => {
        console.error(error);
        this.setState({ apiCallFailed: true });
      }
    );
  };


  onDataChange1(res) {
    if (!this.state.myInputData || this.state.myInputData.length == 0) {
      alert("Serach content can't be blank");

    } else {
      if (this.state.SearchTypeCode == 'ws') {
        this.setState({ youLanguage: "Loading..", showFeedBackPanel: false, srNo: -1 }, () => {
          //https://campuscognitiveservicedevapp.azurewebsites.net
          let start = performance.now();

          let end = performance.now();
          let timeTaken = end - start;
          let min = 0;
          let sec = timeTaken / 1000;
          let times = '';
          if (sec > 59) {
            min = parseInt(sec / 60);
            sec = sec % 60;
          }
          let milsec = timeTaken % 1000;

          times = (min < 10 ? '0' + parseInt(min) : parseInt(min)) + ' Min : ' + (sec < 10 ? '0' + parseInt(sec) : parseInt(sec)) + ' Sec : ' + (milsec < 10 ? '0' + parseInt(milsec) : parseInt(milsec)) + " MS";
          let resultDataFound = [];
          let index = 1;
          if (res && res.results && res.results.length > 0) {
            res.results.forEach(obj => {
              obj.hits.forEach(m => {
                if (m && m.fieldReferenceName && m.fieldReferenceName == 'content') {
                  let myData = '<div style="display:flex;flex-direction:column">';
                  m.highlights.forEach(s => {
                    let innerDiv = '<div style="padding-bottom:6px">';
                    let s1 = s.split("<highlighthit>").join("<div style='display: inline;font-size: 17px;color: red'>");
                    //let s1 = s.replace("<highlighthit>", "<div style='display: inline;background-color: yellow;font-size: 17px;color: green;padding-left: 4;padding-right: 4;}'>");
                    let s2 = s1.split("</highlighthit>").join("</div>");
                    //let s2 = s1.replace("</highlighthit>", "</div>");
                    innerDiv += s2 + "</div>";
                    myData += innerDiv;
                  })

                  myData += '</div>'
                  resultDataFound.push({
                    id: index
                    , Project: obj.project.name
                    , FileName: obj.fileName
                    , Data: { __html: myData }
                    , Path: 'https://dev.azure.com/servicesdocs/' + obj.project.name + '/_wiki/wikis/' + obj.wiki.name
                    , Wiki: obj.wiki.name
                  });
                  index++;
                }

              });
            });

          }
          this.setState({ resultData: resultDataFound, youLanguage: "", showFeedBackPanel: true, tat: times });

        });
      }
    }
  }






  goToPage(e, pageNo) {
    e.stopPropagation();
    this.setState({ currPage: pageNo })
  }
  goToPrevious(e) {
    e.stopPropagation();
    let pageNo = this.state.currPage;
    pageNo = pageNo - 1;
    pageNo = pageNo < 1 ? 1 : pageNo;
    this.setState({ currPage: pageNo })
  }
  goToNext(e) {
    e.stopPropagation();
    let pageNo = this.state.currPage;
    pageNo = pageNo + 1;
    this.setState({ currPage: pageNo })
  }


  onInputChange(val) {
    this.setState({ myInputData: val, youLanguage: '', showFeedBackPanel: (this.state.resultData && this.state.resultData.length > 0 ? true : false) })
  }

  onTypeSelect(e, text, code) {
    this.setState({ SearchType: text, currPage: 1, SearchTypeCode: code, resultData: [], showFeedBackPanel: false });
  }
  render() {
    let result = null;

    let pageNo = this.state.currPage;
    let pageSize = 10;
    let renderedData = this.state.resultData;
    renderedData = renderedData.filter(obj => obj.id >= ((pageSize * (pageNo - 1) + 1)) && obj.id <= (pageSize * pageNo));



    if (renderedData && renderedData.length > 0) {
      if (this.state.SearchTypeCode == 'ws') {
        let resultRow = renderedData.map((obj, index) => {
          return <tr key={obj.id}>
            <td style={{ verticalAlign: "top" }}>{obj.id}</td>
            <td style={{ verticalAlign: "top" }}>{obj.Project}</td>
            <td style={{ verticalAlign: "top" }}>{obj.FileName}</td>
            {this.state.SearchTypeCode == 'ws' ? <td style={{ verticalAlign: "top" }}><div dangerouslySetInnerHTML={obj.Data} /></td> :
              <td style={{ verticalAlign: "top" }}>{obj.Data}</td>}
            <td style={{ verticalAlign: "top" }}>
              <a href="#" onClick={(e) => { window.open(obj.Path) }}>{'Click here'}</a>
            </td>
          </tr >
        })


        result = <table className="blueTable">
          <tr>
            <th style={{ width: "50px" }}>Sr No</th>
            <th>Project</th>
            <th style={{ width: "200px" }}>File Name</th>
            <th style={{ width: "200px" }}>Content</th>
            <th style={{ width: "180px" }}>Navigate</th>
          </tr>
          {
            resultRow
          }
        </table>
      } else {

        let resultRow = renderedData.map((obj, index) => {
          return <tr key={obj.id}>
            <td style={{ verticalAlign: "top" }}>{obj.id}</td>
            <td style={{ verticalAlign: "top" }}>{obj.Project}</td>
            <td style={{ verticalAlign: "top" }}><a href="#" onClick={(e) => { window.open('https://campusvso.visualstudio.com/_git/Campus?path=' + encodeURIComponent(obj.Path)) }}>{obj.Path}</a>
            </td>
          </tr >
        })


        result = <table className="blueTable">
          <tr>
            <th style={{ width: "50px" }}>Sr No</th>
            <th>Project</th>
            <th style={{ width: "200px" }}>Path</th>
          </tr>
          {
            resultRow
          }
        </table>
      }

    }
    else {
      result = <div style={{ textAlign: "center", paddingTop: "100px" }}>Wiki search result not found for content : {this.state.myInputData}</div>
    }


    let backImg = __dirname + 'back.png'; let numUI

    var revenueTableDataF = this.state.resultData;

    let curPage = this.state.currPage;
    let j = this.state.currPage / 5;
    if (parseInt(j.toString()) < this.state.currPage / 5) {
      j++;
    }

    j = parseInt(j.toString());
    let start = (((j - 1) * 5) + 1);
    let end = j * 5;
    let total = revenueTableDataF ? revenueTableDataF.length : 0;
    if (revenueTableDataF && revenueTableDataF.length > 0) {

      let lastrecord = total / 10;
      let isLastReached = false;
      if (revenueTableDataF && revenueTableDataF.length > 10) {
        let numList;
        let array = [];
        for (var s = start; s <= end; s++) {
          if (s <= Math.ceil(lastrecord))
            array.push(s);
          else {
            isLastReached = true;
          }
        }
        numList = array.map((obj, i) => {
          let classNames = "";
          if (obj == this.state.currPage)
            classNames = "activeN";
          return <div key={i} className={classNames} href="#" onClick={(e) => this.goToPage(e, obj)}>{obj}</div>
        })

        let totalend = end + 1;

        numUI = <div className="links" style={{ display: "flex", minWidth: "210px", justifyContent: "space-between" }}>

          {
            this.state.currPage == 1 ?

              <div href="#" style={{ backgroundColor: "lightgrey" }}>&laquo;</div>
              :
              <div href="#" onClick={(e) => this.goToPrevious(e)}>&laquo;</div>
          }
          {numList}
          {
            Math.ceil(lastrecord) == this.state.currPage ?
              <div href="#" style={{ backgroundColor: "lightgrey" }}>&raquo;</div>
              :
              <div href="#" onClick={(e) => this.goToNext(e)}>&raquo;</div>
          }
        </div>
      }
    }
    return (
        <div>
          <div>
            <div style={{ padding: "5px", paddingLeft: "15px", fontSize: "25px", backgroundColor: "#1C6EA4", color: "white" }}>Azure DevOps Search</div>
            <div style={{ paddingLeft: "15px", paddingTop: "25px", display: "flex" }}>
            <div style={{ padding: "5px" }}>Search Text</div>
              <div style={{ display: "inline" }}>
                <input type="text" style={{ padingLeft: "2px", height: "27px", width: "270px", fontSize: "15px" }} value={this.state.myInputData} onChange={(e) => this.onInputChange(e.target.value)} />
                &nbsp;&nbsp;<input type="button"  onClick={(e) => this.callLoginPopupAPI(e, 'wiki')} style={{ padingLeft: "2px", height: "27px", width: "80px", fontSize: "15px" }} value="Search" 
                 /></div>
             
              
            </div>
            <br />
          </div>

          {this.state.youLanguage && this.state.youLanguage.length > 0 ? <div style={{ display: "inline", paddingLeft: "15px", paddingTop: "100px" }}>{this.state.youLanguage}</div> : null}
          {this.state.showFeedBackPanel ? <div>
            <div style={{ display: "flex", justifyContent: "space-between", padding: "15px", fontWeight: "bold", fontSize: "20px" }}>

              <div style={{ display: "inline", fontSize: "14px", fontWeight: "normal", paddingTop: "4px" }}>
              &nbsp;</div>


              {numUI}
            </div> <div className="headerPanel">
              <div style={{ minHeight: "406px", border: "1px solid lightgray", overflow: "auto" }}>
                {result}
              </div></div></div> : null}
        </div>
    
    )
  }
}

export default WikiCode;


