import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import AuthService from './services/auth.service';
import GraphService from './services/graph.service';
import WikiCode from './WIKICode.js'

class App extends Component {
  constructor() {
    super();
    this.authService = new AuthService();
    this.graphService = new GraphService();
    this.state = {
      userInfo: null,
      apiCallFailed: false,
      tabId: 0,
      isCampusAPI: 0
    };
  }


  getUserInfo(token, type) {
    if (type == 'team') {
      this.graphService.getTeamsData(token).then(
        data => {
          this.setState({ userInfo: data });
        },
        error => {
          console.error(error);
          this.setState({
            apiCallFailed: true
          });
        }
      );
    }
    else if (type == 'campusapi') {
      this.graphService.getCampusWebAPIData(token).then(
        data => {
          console.log('data : ', data);
          let obj = { value: [{ displayName: JSON.stringify(data) }] }
          this.setState({ userInfo: obj });
        },
        error => {
          console.error(error);
          this.setState({
            apiCallFailed: true
          });
        }
      );
    }
  }

  callLoginPopupAPI = (type) => {
    this.setState({ apiCallFailed: false, userInfo:null,isCampusAPI: (type == 'wiki' ? 1 : 0) });
    if(type=='team' || type=='campusapi')
    {
    this.authService.getTokenWithLoginPopup(type).then(
      token => {
        this.getUserInfo(token, type);
      },
      error => {
        console.error(error);
        this.setState({ apiCallFailed: true });
      }
    );
  }
  };


  callLoginRedirectAPI = (type) => {
    this.setState({ apiCallFailed: false });
    this.authService.getTokenWithLoginRedirect(type).then(
      token => {
        this.getUserInfo(token, type);
      },
      error => {
        console.error(error);
        this.setState({ apiCallFailed: true });
      }
    );
  };


  callAccessTokenRedirectAPI = (type) => {
    this.setState({ apiCallFailed: false });
    this.authService.getTokenWithRedirect(type).then(
      token => {
        this.getUserInfo(token, type);
      },
      error => {
        console.error(error);
        this.setState({ apiCallFailed: true });
      }
    );
  };


  callAccessTokenPopupAPI = (type) => {
    this.setState({ apiCallFailed: false });
    this.authService.getTokenWithPopup(type).then(
      token => {
        this.getUserInfo(token, type);
      },
      error => {
        console.error(error);
        this.setState({ apiCallFailed: true });
      }
    );
  };

  ontabClick(tabID, e) {
    this.setState({ tabId: tabID, userInfo: null, isCampusAPI: 0 });
  }

  render() {
    let templates = [];
    let userInfo = this.state.userInfo;
    if (this.state.isCampusAPI == 1) {
      templates.push(<WikiCode />);
    }
    else {
      templates.push(
        <div style={{ padding: "10px", textAlign: "left", fontSize: "20px", fontWeight: "bold" }}> API Respose Result</div>
      );
      if (userInfo && userInfo.value) {
        userInfo.value.forEach((obj, i) => {
          templates.push(
            <div style={{ padding: "5px", textAlign: "left" }} key={i}>{i + 1} . {obj.displayName}</div>
          );
        })

      }
    }
    if (this.state.apiCallFailed) {
      templates.push(
        <strong key="apiCallFailed">Graph API call unsuccessful</strong>
      );
    }
    return (
      <div className="App">

        <div style={{ display: 'flex' }} className="w3-bar w3-black">
          <div style={{ display: "flex", flexDirection: "column", padding: "5px", borderRight: "3px solid white", backgroundColor: "black" }}>
            <img src={logo} className="App-logo" alt="logo" />
            <div> MSAL.js</div>
          </div>
          <div onClick={(e) => this.ontabClick(0, e)} className={this.state.tabId == 0 ? "w3-bar-item w3-button tablink w3-red" : "w3-bar-item w3-button tablink"}>Home</div>
          <div onClick={(e) => this.ontabClick(1, e)} className={this.state.tabId == 1 ? "w3-bar-item w3-button tablink w3-red" : "w3-bar-item w3-button tablink"}>Teams (loginPopup)</div>
          <div onClick={(e) => this.ontabClick(2, e)} className={this.state.tabId == 2 ? "w3-bar-item w3-button tablink w3-red" : "w3-bar-item w3-button tablink"}>Teams (loginRedirect)</div>
          <div onClick={(e) => this.ontabClick(3, e)} className={this.state.tabId == 3 ? "w3-bar-item w3-button tablink w3-red" : "w3-bar-item w3-button tablink"}>Teams (accessTokenPopup)</div>
          <div onClick={(e) => this.ontabClick(4, e)} className={this.state.tabId == 4 ? "w3-bar-item w3-button tablink w3-red" : "w3-bar-item w3-button tablink"}>Teams (acessTokenRedirect)</div>
        </div>


        {

          this.state.tabId == 0 ?
            <div style={{ isplay: "flex" }}>
              <div className="rcrners2">
                Microsoft Authentication Library (MSAL) enables developers to acquire tokens from the Microsoft identity platform endpoint in order to access secured web APIs. These web APIs can be the Microsoft Graph, other Microsoft APIs, third-party web APIs, or your own web API.
               <h2>Click on the above tab to get the access token to call the Microsoft Graph API to get the teams detals</h2>
              </div>
            </div>

            : this.state.tabId == 1 ?
              <div key="loggedIn">
                <div style={{ textAlign: "left", padding: "10px" }}><h2>loginPopup()</h2>
               When we did not get the access token from the acquireTokenSilent(), loginPopup() open the login popup and after successful login, popup get closed and we will get the token.
                  <div style={{ paddingTop: "15px" }}>
                    <input onClick={() => this.callLoginPopupAPI('team')} value="Click to get Joined Teams" type="button" />&nbsp;&nbsp;
                    <input onClick={() => this.callLoginPopupAPI('wiki')} value="Click to get Wiki Data" type="button" />
                    &nbsp;&nbsp;<input onClick={() => this.callLoginPopupAPI('campusapi')} value="Click to get Campus API Data" type="button" />

                  </div>
                </div>

                <div>
                  <div style={{ display: "flex", flexDirection: "column" }}>
                    {templates}
                  </div>
                </div>
              </div>
              : this.state.tabId == 2 ?
                <div key="loggedIn">
                  <div style={{ textAlign: "left", padding: "10px" }}><h2>loginPopup()</h2>
                  When we did not get the access token from the acquireTokenSilent(), loginRedirect() open the login in the same window and after successful login, it will be redirected to home page, user need to call the api again to get the result.
                    <div style={{ paddingTop: "15px" }}>
                      <input onClick={() => this.callLoginRedirectAPI('team')} value="Click to get Joined Teams" type="button" />
                    </div>
                  </div>

                  <div>
                    <div style={{ display: "flex", flexDirection: "column" }}>
                      {templates}
                    </div>
                  </div>
                </div>
                : this.state.tabId == 3 ?
                  <div key="loggedIn">
                    <div style={{ textAlign: "left", padding: "10px" }}><h2>accessTokenPopup()</h2>
                   When we did not get the access token from the acquireTokenSilent(), accessTokenPopup() open the login popup and after successful login, popup get closed and we will get the token.
                   <div style={{ padding: "5px", paddingTop: "15px", fontWeight: "bold" }}>if user is not logged in then we get the error.<div style={{ padding: "5px", paddingLeft: "0px" }}><span style={{ backgroundColor: "yellow", color: "red" }}>User login is required. For silent calls, request must contain either sid or login_hint </span></div></div>
                      <div style={{ paddingTop: "15px" }}>
                        <input onClick={() => this.callAccessTokenPopupAPI('team')} value="Click to get Joined Teams" type="button" />
                      </div>
                    </div>

                    <div>
                      <div style={{ display: "flex", flexDirection: "column" }}>
                        {templates}
                      </div>
                    </div>
                  </div>

                  : this.state.tabId == 4 ?
                    <div key="loggedIn">
                      <div style={{ textAlign: "left", padding: "10px" }}><h2>accessTokenRedirect()</h2>
                       When we did not get the access token from the acquireTokenSilent(), accessTokenRedirect() open the login popup and after successful login, popup get closed and we will get the token.
                     <div style={{ padding: "5px", fontWeight: "bold" }}>if user is not logged in then we get the error.<div style={{ padding: "5px" }}><span style={{ backgroundColor: "yellow", color: "red" }}>User login is required. For silent calls, request must contain either sid or login_hint </span></div></div>
                        <div style={{ paddingTop: "15px" }}>
                          <input onClick={() => this.callAccessTokenRedirectAPI('team')} value="Click to get Joined Teams" type="button" />
                        </div>
                      </div>

                      <div>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                          {templates}
                        </div>
                      </div>
                    </div>
                    :
                    <div>TEst</div>
        }




      </div>
    );
  }
}

export default App;



